@import "../../color.scss";

.personCart {
  position: relative;
  height: 430px;
  width: 240px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0 10px 10px;
  padding-bottom: 10px;
  overflow: hidden;
  border: 1px solid $color2;
  border-radius: 15px;
  color: $color3;
  .img {
    width: 100%;
    height: 75%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  h3 {
    font-size: 1rem;
    text-align: center;
  }
}
